
import { defineComponent } from 'vue';
import { MatomoDialog } from 'CoreHome';

export default defineComponent({
  components: {
    MatomoDialog,
  },
  computed: {
    showDialog() {
      return this.count > 15;
    },
  },
  data() {
    return {
      count: 12,
    };
  },

  setup() {
    return {
      increment() {
        this.count += 1;
      },
      decrement() {
        this.count -= 1;
      },
    };
  },
});
